.Input {
  width: 220px;
  height: 45px;
  position: relative;

  input {
    display: block;
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.05);
    border: none;
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    line-height: 16px;
    color: #555555;
    padding: 18px;
    box-sizing: border-box;

    &:focus {
      outline: 4px solid lighten(#836BE0, 20%);
    }
  }

  span {
    position: absolute;
    right: 15px;
    font-size: 14px;
    line-height: 14px;
    display: block;
    top: 50%;
    transform: translateY(-50%);
    color: #f00;
  }
}